'use client';

import { useState, useEffect } from 'react';

interface VoiceInputProps {
  onTranscribe: (text: string) => void; // Callback to pass transcribed text
}

export default function VoiceInput({ onTranscribe }: VoiceInputProps) {
  const [listening, setListening] = useState(false);
  const [transcript, setTranscript] = useState('');

  const SpeechRecognition =
    (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;

  const recognition = SpeechRecognition ? new SpeechRecognition() : null;

  useEffect(() => {
    if (!recognition) return;

    recognition.continuous = false;
    recognition.interimResults = true;
    recognition.lang = 'en-US';

    recognition.onresult = (event: any) => {
      let finalTranscript = '';
      for (let i = 0; i < event.results.length; i++) {
        finalTranscript += event.results[i][0].transcript;
      }
      setTranscript(finalTranscript);
      onTranscribe(finalTranscript);
    };

    recognition.onend = () => {
      setListening(false);
    };
  }, [recognition, onTranscribe]);

  const handleStart = () => {
    if (!recognition) return alert('Speech Recognition not supported in this browser');
    setListening(true);
    recognition.start();
  };

  const handleStop = () => {
    if (!recognition) return;
    recognition.stop();
    setListening(false);
  };

  return (
    <div className="flex items-center gap-3">
      <input
        type="text"
        value={transcript}
        onChange={(e) => {
          setTranscript(e.target.value);
          onTranscribe(e.target.value);
        }}
        placeholder="Speak your expense..."
        className="flex-1 border rounded-lg px-3 py-2 dark:bg-gray-700 dark:text-white"
      />
      <button
        onClick={listening ? handleStop : handleStart}
        className={`px-4 py-2 rounded-lg text-white ${
          listening ? 'bg-red-500' : 'bg-green-500'
        }`}
      >
        {listening ? 'Stop üé§' : 'Speak üé§'}
      </button>
    </div>
  );
}
